import React from "react";
import { Box, Heading, Text, Center, Spinner, Stack } from "@chakra-ui/react";
import axios from "axios";
import config from "../config";
import Film from "../components/Film";
import { Carousel } from "react-responsive-carousel";
import "../css/Carousel.css";
import { Link as RLink } from "react-router-dom";

function Home({ isMobile }: { isMobile: boolean }) {
	const [newFilms, setNewFilms] = React.useState<any>(null);
	const [newFilmsLoaded, setNewFilmsLoaded] = React.useState<boolean>(false);

	const [topFilms, setTopFilms] = React.useState<any>(false);
	const [topFilmsLoaded, setTopFilmsLoaded] = React.useState<any>(false);

	const [bestFilms, setBestFilms] = React.useState<any>(false);
	const [bestFilmsLoaded, setBestFilmsLoaded] = React.useState<any>(false);

	React.useEffect(() => {
		const getNewFilms = async () => {
			let month: string | number = new Date().getMonth();
			if (month === 0) month = "JANUARY";
			if (month === 1) month = "FEBRUARY";
			if (month === 2) month = "MARCH";
			if (month === 3) month = "APRIL";
			if (month === 4) month = "MAY";
			if (month === 5) month = "JUNE";
			if (month === 6) month = "JULY";
			if (month === 7) month = "AUGUST";
			if (month === 8) month = "SEPTEMBER";
			if (month === 9) month = "OCTOBER";
			if (month === 10) month = "NOVEMBER";
			if (month === 11) month = "DECEMBER";

			let { data: res }: any = await axios.get(
				`${
					config.search_url
				}/films/releases?year=${new Date().getFullYear()}&month=${month}&page=1`,
				{
					headers: {
						"X-API-KEY": config.api_token,
					},
				}
			);

			setNewFilms(res.releases);
			setNewFilmsLoaded(true);
		};

		const getTopFilms = async () => {
			const { data: res } = await axios.get(
				`${config.api_url}/films/top?type=TOP_100_POPULAR_FILMS`,
				{
					headers: {
						"X-API-KEY": config.api_token,
					},
				}
			);

			setTopFilms(res.films);
			setTopFilmsLoaded(true);
		};

		const getBestFilms = async () => {
			const { data: res } = await axios.get(
				`${config.api_url}/films/top?type=TOP_250_BEST_FILMS`,
				{
					headers: {
						"X-API-KEY": config.api_token,
					},
				}
			);

			setBestFilms(res.films);
			setBestFilmsLoaded(true);
		};

		getNewFilms();
		getTopFilms();
		getBestFilms();
	}, []);

	return (
		<Box paddingTop={!isMobile ? 10 : 0}>
			{!isMobile && (
				<Box
					paddingLeft={[3, 10]}
					paddingRight={[3, 10]}
					paddingInlineStart={"1rem"}
					paddingInlineEnd={"1rem"}
					paddingTop={10}
					paddingBottom={10}
				>
					<Center>
						<Heading color="brand.500" size="2xl" fontWeight="800">
							KinoSearch
						</Heading>
					</Center>
					<Center>
						<Text fontSize={["sm", "lg"]}>
							Лучший сервис для просмотра кино и сериалов
						</Text>
					</Center>
				</Box>
			)}
			{(!isMobile && (
				<Box
					paddingLeft={[3, 10]}
					paddingRight={[3, 10]}
					paddingInlineStart={"1rem"}
					paddingInlineEnd={"1rem"}
					paddingTop={[10, 20]}
				>
					<Heading size="md" fontWeight="700">
						Новинки
					</Heading>
					<Box paddingTop={5}>
						{(!newFilmsLoaded && (
							<Box w="full">
								<Center>
									<Spinner color="brand.500" size="xl"></Spinner>
								</Center>
							</Box>
						)) || (
							<Stack
								direction="row"
								overflow="auto"
								overflowX="auto"
								spacing="20px"
							>
								{newFilms.map((film: any) => (
									<Film film={film} />
								))}
							</Stack>
						)}
					</Box>
				</Box>
			)) || (
				<>
					{(!newFilmsLoaded && (
						<Box w="full">
							<Center>
								<Spinner color="brand.500" size="xl"></Spinner>
							</Center>
						</Box>
					)) || (
						<>
							<Stack
								direction="row"
								justifyContent="space-between"
								position="absolute"
								zIndex={3}
								paddingLeft={[3, 10]}
								paddingTop={5}
								paddingRight={[3, 10]}
								paddingInlineStart={"1rem"}
								paddingInlineEnd={"1rem"}
								w="100%"
							>
								<Heading size="lg" fontWeight="800">
									Новинки
								</Heading>

								<Heading color="brand.500" fontWeight="800" size="lg">
									KinoSearch
								</Heading>
							</Stack>
							<Carousel
								showArrows={false}
								autoPlay={true}
								showThumbs={false}
								infiniteLoop={true}
								showStatus={false}
							>
								{newFilms.map((film: any) => (
									<Box
										as={RLink}
										to={`/film/${film.kinopoiskId || film.filmId}`}
									>
										{/* <p className="title">Новинки</p> */}
										<img
											alt={film.nameRu || film.nameEn || film.nameOriginal}
											src={film.posterUrlPreview}
										/>
										<p className="legend">
											{film.nameRu || film.nameEn || film.nameOriginal}
										</p>
									</Box>
								))}
							</Carousel>
						</>
					)}
				</>
			)}
			<Box
				paddingLeft={[3, 10]}
				paddingRight={[3, 10]}
				paddingInlineStart={"1rem"}
				paddingInlineEnd={"1rem"}
				paddingTop={[10, 20]}
			>
				<Heading size="md" fontWeight="700">
					Популярное
				</Heading>
				<Box paddingTop={5}>
					{(!topFilmsLoaded && (
						<Box w="full">
							<Center>
								<Spinner color="brand.500" size="xl"></Spinner>
							</Center>
						</Box>
					)) || (
						<Stack
							direction="row"
							overflow="auto"
							overflowX="auto"
							spacing="20px"
						>
							{topFilms.map((film: any) => (
								<Film film={film} />
							))}
						</Stack>
					)}
				</Box>
			</Box>
			<Box
				paddingLeft={[3, 10]}
				paddingRight={[3, 10]}
				paddingInlineStart={"1rem"}
				paddingInlineEnd={"1rem"}
				paddingTop={[10, 20]}
			>
				<Heading size="md" fontWeight="700">
					Лучшее
				</Heading>
				<Box paddingTop={5}>
					{(!bestFilmsLoaded && (
						<Box w="full">
							<Center>
								<Spinner color="brand.500" size="xl"></Spinner>
							</Center>
						</Box>
					)) || (
						<Stack
							direction="row"
							overflow="auto"
							overflowX="auto"
							spacing="20px"
						>
							{bestFilms.map((film: any) => (
								<Film film={film} />
							))}
						</Stack>
					)}
				</Box>
			</Box>
		</Box>
	);
}

export default Home;
